/* Hero */
.hero {
  margin-top: 30px;

  @media screen and (min-width: 768px) and (max-width: 1279.98px) {
    margin-top: 10px;
  }

  @media screen and (min-width: 1280px) {
    margin-top: 55px;
  }
}

.container_hero {
  @media screen and (max-width: 767.98px) {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.hero_title {
  max-width: 490px;
  margin-bottom: 15px;
  font-size: 64px;
  line-height: 1.02;
  font-family: var(--title-font);

  @media screen and (max-width: 1279.98px) {
    max-width: 224px;
    margin-bottom: 3px;
    font-size: 30px;
    line-height: 38px;
    text-align: center;
  }
}

.hero_text {
  margin-bottom: 30px;
  font-weight: 500;
  font-size: 24px;
  line-height: 1.21;

  @media screen and (max-width: 767.98px) {
    margin-bottom: 35px;
    font-size: 16px;
    line-height: 1.25;
    text-align: center;
  }

  @media screen and (min-width: 768px) and (max-width: 1279.98px) {
    margin-bottom: 25px;
    font-size: 16px;
    line-height: 1.25;
  }
}

.hero_icon {
  display: flex;
  width: 25px;
  height: 20px;

  @media screen and (max-width: 1279.98px) {
    width: 19px;
    height: 15px;
  }
}

.hero_btn {
  // margin-bottom: 65px;
  text-decoration: none;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1),
  color 250ms cubic-bezier(0.4, 0, 0.2, 1),
  background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover {
    transform: scale(1.15);

  }
  //   @media screen and (max-width: 767.98px) {
  //     margin-bottom: 45px;
  //   }

  //   @media screen and (min-width: 768px) and (max-width: 1279.98px) {
  //     margin-bottom: 30px;
  //   }
}

.hero_link {
  padding: 10px 44px;
}

.hero_list {
  display: flex;
}

.hero_item:not(:last-child) {
  margin-right: 12px;

  @media screen and (max-width: 1279.98px) {
    margin-right: 10px;
  }
}

.hero_spoon {
  position: absolute;
  left: 0;
  top: 165px;

  @media screen and (max-width: 1279.98px) {
    display: none;
  }
}
